@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "Jaapokkisubtract";
  src: url("../app/fonts/jaapokkisubtract.woff2");
}
@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue";
  src: url("../app/fonts/helveticaneue.woff2");
}
@font-face {
  font-weight: 300;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Light";
  src: url("../app/fonts/helveticaneue-light.woff2");
}
@font-face {
  font-weight: 500;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Medium";
  src: url("../app/fonts/helveticaneue-medium.woff2");
}
@font-face {
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Bold";
  src: url("../app/fonts/helveticaneue-bold.woff2");
}
:root {
  --font-body: "HelveticaNeue", sans-serif;
  --font-title: "Jaapokkisubtract", sans-serif;
  --transition: .2s;
}

:root {
  --special: hsl(229, 86%, 58%);
  --text: hsl(0, 0%, 10%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  letter-spacing: 0.1rem;
  font-family: var(--font-body);
}

.arrow {
  position: relative;
  display: inline-block;
  min-width: 3.3125rem;
  min-height: 2.0625rem;
  background: url("../images/tools/arrow-right.svg");
}
.arrow__left {
  margin-right: 2.625rem;
  background-image: url("../images/tools/arrow-light.svg");
}

.divider {
  min-height: 0.1875rem;
  max-width: 12.5rem;
  margin-left: 1.25rem;
  background-color: var(--white);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-title);
}

p {
  margin: 0;
}

a,
a:visited,
a:active {
  color: var(--white);
  text-decoration: none;
  transition: var(--transition) ease-in-out;
}

.container {
  padding: 0 1.25rem;
  max-width: 83.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    transform: translate(0, 0);
    padding: 0;
  }
}

.header {
  background-image: linear-gradient(var(--special) 50%, var(--text) 50%);
  color: var(--white);
  font-weight: 700;
}
.header__inner {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.375rem 0;
}
.header__menu *:not(:last-child) {
  margin-right: 1.875rem;
  float: left;
}
.header__logo {
  transform: translate(-50%);
}
.header__main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
}
.header__promo {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 0;
  min-height: 43.75rem;
  background-image: url("../images/header.webp");
}
.header__promo::before {
  content: "01";
  font-size: 4.375rem;
  color: var(--text);
  position: absolute;
  top: 2.5rem;
  left: -3.75rem;
  padding: 0.9375rem 1.25rem 0;
  background-color: var(--white);
  font-family: var(--font-title);
}
.header__promo::after {
  content: "";
  position: absolute;
  top: 11.625rem;
  left: -1.625rem;
  min-width: 0.1875rem;
  min-height: 3.125rem;
  background-color: var(--white);
}
.header__buttons {
  position: absolute;
  top: 5.625rem;
  left: 6.875rem;
  font-size: 0;
}
.header__buttons .arrow::before {
  content: "";
  position: absolute;
  top: -0.625rem;
  left: -0.625rem;
  z-index: -1;
  min-width: 4.6875rem;
  min-height: 1.6875rem;
  background-color: var(--special);
}
.header__text {
  margin-left: 6.25rem;
  font-size: 1.75rem;
}
.header__text h1 {
  font-size: 5.375rem;
  letter-spacing: 0.5rem;
  position: relative;
}
.header__text h1::before {
  content: "";
  position: absolute;
  top: -0.9375rem;
  left: -2.3125rem;
  min-height: 0.1875rem;
  min-width: 6.25rem;
  background-color: var(--white);
}
.header__text p {
  margin-top: 3.125rem;
}
.header__text p .arrow {
  top: 0.375rem;
  margin-right: 1.25rem;
}
.header__text p b {
  font-family: "HelveticaNeue Bold";
}
.header__aside {
  position: absolute;
  right: -7.5rem;
  bottom: 17.5rem;
  transform: rotate(90deg);
}
.header__aside::before {
  content: url("../images/tools/grid.svg");
  position: absolute;
  height: 2.8125rem;
  width: 2.8125rem;
  top: -0.75rem;
  left: -7.8125rem;
}
.header__aside::after {
  content: "01";
  position: absolute;
  top: -1.375rem;
  right: -5.3125rem;
  padding-top: 2.6875rem;
  border-top: 0.1875rem solid var(--white);
  transform: rotate(-90deg);
}